  z-index: 2;
  margin: 0;
  font-size: 36px;
  font-weight: 800;
  line-height: 35px;
  -webkit-text-stroke: 1px;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 47px;
}
.advantages h2::after {
  position: absolute;
  left: 0;
  bottom: 0;
  font-size: 56px;
  font-weight: 800;
  line-height: 55px;
  -webkit-text-stroke: 1px;
  color: #2d383f;
  opacity: 0.03;
  content: attr(data-content);
}
@media (max-width: 1199px) {
  .advantages h2 {
    font-size: 25px;
    line-height: 36px;
    letter-spacing: 0.01em;
  }
  .advantages h2::after {
    font-size: 39px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    font-size: 22px;
    line-height: 22px;
  }
  .advantages h2::after {
    font-size: 26px;
    line-height: 26px;
  }
}
.advantages h2::after {
  left: -28%;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .advantages h2 {
    margin-bottom: 41px;
