    margin-top: 26px;
  }
}

.footer__social-link {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: 1px solid rgba(45, 56, 63, 0.3);
  transition: outline 0.3s ease-in, background-color 0.3s ease-in;
}
.footer__social-link svg {
  fill: #2d383f;
  transition: fill 0.3s ease-in;
}
.footer__social-link:focus-visible {
  outline-color: #2d383f;
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) {
    outline-color: #2d383f;
  }
  .footer__social-link:hover:not(.focus-visible):active {
    background-color: #0266c1;
    outline: none;
  }
  .footer__social-link:hover:not(.focus-visible):active svg {
    fill: #ffffff;
  }
}
.footer__social-link.focus-visible:focus {
  outline-color: #2d383f;
}
.footer__social-link.focus-visible:focus:active {
  background-color: #0266c1;
  outline: none;
}
.footer__social-link.focus-visible:focus:active svg {
  fill: #ffffff;
}
.footer__social-link:active {
  background-color: #0266c1;
  outline: none;
}
.footer__social-link:active svg {
  fill: #ffffff;
}
.footer__social-link:disabled {
  outline-color: #2d383f;
  opacity: 0.3;
}

.footer__copyright p {
  margin: 20px 0 0;
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
  text-align: center;
}
@media (max-width: 767px) {
  .footer__copyright p {
    text-align: unset;
  }
}

